#headerContainer{
    flex: 1;
    display: flex;
    flex-direction: row;
    padding: 5vmin;
    gap: 5vmin;
}

    #headerChild0{
        flex: 1;
        display: flex;
        position: relative;
    }

        #headerChild0Child0Img{
            object-fit: contain;
            width: 75%;
        }

        #headerChild0Child0{
            flex: 1;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 1;
            opacity: 0%;
        }

    #headerChild1{
        flex: 5;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        font-size: 6.5vmin;
    }

        #headerChild1Child0{
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 1;
            opacity: 0%;
        }


        #headerChild1Child1Img{
            object-fit: contain;
            width: 100%;
            height: 125%;
        }

    #headerChild2{
        flex: 1;
        display: flex;
        position: relative;
    }

        #headerChild2Child0Img{
            object-fit: contain;
            width: 75%;
        }

        #headerChild2Child0{
            flex: 1;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 1;
            opacity: 0%;
        }

